import React from 'react';
import MaterialTable from 'material-table';
import Toolbar from '@material-ui/core/Toolbar';
import Grid from '@material-ui/core/Grid';

import './font_icons.css';

function ExpandableTable(props) {

  return (

    <MaterialTable

      columns={[

        { title: 'Report Name', field: 'reportName', headerStyle: {
					  fontWeight: 'bold',
					  
					} },

        { title: 'Owner Name', field: 'owner', headerStyle: {
					  fontWeight: 'bold',
					  
					} },
        
        

        

      ]}
      


      data={[

        { reportName: 'report1_v1.xlsx', owner: 'Virat Kohli'},

        { reportName: 'report2_v1.xlsx', owner: 'Gilchrist'},
		
		{ reportName: 'report3_v1.xlsx', owner: 'Brett Lee'},
		{ reportName: 'report4_v1.xlsx', owner: 'Damien Martin'},
		{ reportName: 'report5_v1.xlsx', owner: 'Andrew Symonds'},
		

      ]}
      
      options={{
          headerStyle: {
            backgroundColor: 'black',
            color: '#FFF'
          }
          }
          }

      title="5 Matching Results"

      detailPanel={rowData => {

        return (
				<Toolbar>
				
				<Grid container alignItems="center" justify="center">
				
				<Grid item>
				
        <MaterialTable
		  
				options={{
				  search: false,
				  paging: false
				}}

				  columns={[

					{ title: 'Users', field: 'userName' ,
					
					headerStyle: {
					  fontWeight: 'bold',
					  backgroundColor: 'black',
					  color: 'white'
					}
					
					},

					
				  ]}

				  data={[

					{ userName:'Brett Lee'},

					{ userName:  'Gilchrist'},
					
					{ userName:  'Ricky Ponting'},
					
					{ userName:  'Andrew Symonds'},

				  ]}

				  title=""
	  
	  />
	  
	  </Grid>
	  
	  </Grid>
	  </Toolbar>
	  

        )

      }}

      onRowClick={(event, rowData, togglePanel) => togglePanel()}

    />

  )

}

export default  ExpandableTable;
