function TabPanel(props) {
  const { children, value, index, ...other } = props;
  
  //console.log(value, index)

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};


function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}



function Header(props) {
  const { classes, onDrawerToggle } = props;
  
  const [value, setValue] = React.useState(0);
  
  const handleChange = (event, newValue) => {
	  

    setValue(newValue);
  };
  
  const handleChangeIndex = index => {
	  
	  
    setValue(index);
	
  };

  return (
    <React.Fragment>
      <AppBar color="primary" position="sticky" elevation={0} styles={{justify: 'center'}}>
      
      <Grid container justify="center">
      
      
      	<Grid item xs = {12 } justify="center" className={classes.titlebar}>
        <Toolbar className={classes.toolbar}>
   
						<img src="https://www.telstra.com.au/etc/designs/comp-nav/assets/t-logo-twilight.svg" alt="logo" className={classes.logo} />
					
						<Typography variant="h4" edge="start" className={classes.title} >
			      	Telstra Metric Search
			    	</Typography>
							           

        </Toolbar>
        
        
        </Grid>
        
      
       </Grid>
      
        <Tabs value={value} textColor="inherit" onChange={handleChange}>
					 <Tab textColor="inherit" label="Metric Search" {...a11yProps(0)} className={classes.tab_styles}/>
					 <Tab textColor="inherit" label="User Management"  {...a11yProps(1)} className={classes.tab_styles}/>
					 <Tab textColor="inherit" label="Owner Management" {...a11yProps(2)} className={classes.tab_styles}/>
				      
        </Tabs>
        
    </AppBar>
      <AppBar
        component="div"
        className={classes.secondaryBar}
        color="primary"
        position="static"
        elevation={0}
      >
		
		<TabPanel value={value} index={0} className={classes.main}>
			<AccordianTable />
		</TabPanel>
		<TabPanel value={value} index={1} className={classes.main}>
			<DisableFieldEditable individual="Users"/>
		</TabPanel>
		<TabPanel value={value} index={2} className={classes.main}>
		  <DisableFieldEditable individual="Owners"/>
		</TabPanel>
      </AppBar>
    </React.Fragment>
  );
}

Header.propTypes = {
  classes: PropTypes.object.isRequired,
  onDrawerToggle: PropTypes.func.isRequired,
};

export default withStyles(styles)(Header);
